import React, { Component } from 'react';
import { Carousel } from 'react-bootstrap';
import firstImg from '../assets/fa1.jpg';
import secondImg from '../assets/fa2.jpg';

export default class CarouselBox extends Component {
    render() {
        return (
            <Carousel>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={ firstImg }
                        alt="The first one"

                    />
                    <Carousel.Caption>
                        <h3>Hui pizda skovoroda</h3>
                        <p>Gavno zalupa penis her</p>

                    </Carousel.Caption>
                </Carousel.Item>
                <Carousel.Item>
                    <img
                        className="d-block w-100"
                        src={ secondImg }
                        alt="The second one"

                    />
                    <Carousel.Caption>
                        <h3>Hui pizda skovoroda dva</h3>
                        <p>Gavno zalupa penis her dva</p>

                    </Carousel.Caption>
                </Carousel.Item>
            </Carousel>

        )
    }
}