                                        <div class="card-body">Занимался сборкой ПК, подбором комлпектующих, базируясь на использовании дешевых б/у серверных компонентов из Китая. В результате удавалось собирать игровые ПК хорошей мощности по цене в 5 раз ниже аналогичных по производительности комплектаций из магазинов.</div>
